import React, { memo } from "react";
import MaliganFormtem from "./MaliganFormtem";

const MaliganForm = ({form}) => {
    return (
        <>
            <MaliganFormtem form={form} name={"USM"} />
            <MaliganFormtem form={form} name={"Blood"} />
            <MaliganFormtem form={form} name={"Lungs"} />
            <MaliganFormtem form={form} name={"Reason"} />
        </>
    );
};

export default memo(MaliganForm);
